import React from 'react' ;
import { NavLink } from 'react-router-dom';
import Logo from '@/assets/images/logo.png';
import { Popover } from 'antd';
import {
  MenuOutlined,
} from '@ant-design/icons';
import './styles.less';


const menus = [
  {
    label: 'About',
    path: '/',
  },
  {
    label: 'Blog',
    path: '/a',
  },
  {
    label: 'Contact Us',
    path: '/b',
  },
  {
    label: 'Career',
    path: '/c',
  },
];

const content = (
  <div>
    {menus.map((item) => (
      <div className="p-5 md:px-3 cursor-pointer font-semibold text-base" key={item.label}>
        <NavLink  style={({ isActive }) => ({ color: isActive ? "#22DFEB" : "#FFFFFF", fontWeight: isActive ? '700' : '400' })} to={item.path}>{item.label}</NavLink>
      </div>
    ))}
  </div>
);

function Header() {
  return (
    <div className=' fixed w-full py-10 z-10 md:py-6'>
      <div className="'min-w-[100vw]'">
        <div className="flex justify-between items-center px-5 md:mt-3">
          <div className="w-auto flex justify-center items-center">
            <img src={Logo} alt="" className='' />
          </div>
          <div className='flex items-center'>
            <div className='md:block lg:hidden xl:hidden 2xl:hidden'>
              <Popover placement="bottomRight" content={content} trigger="click" color='#000'>
                <MenuOutlined style={{color: '#fff', fontSize: '20px'}}/>
              </Popover>
            </div>
            {/* <CloseOutlined /> */}
            <div className={'flex justify-between items-center md:hidden'}>
              {menus.map((item) => (
                <div className="p-5 md:px-3 cursor-pointer font-semibold text-base" key={item.label}>
                  <NavLink  style={({ isActive }) => ({ color: isActive ? "#22DFEB" : "#FFFFFF", fontWeight: isActive ? '700' : '400' })} to={item.path}>{item.label}</NavLink>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Header;
