import React, {useState, useEffect} from 'react';
import '../styles.less'
import { Transition } from 'react-transition-group';
import { useRef } from 'react';

const duration = 3000;

const defaultStyle = {
  transition: `opacity ${duration}ms ease-in-out`,
  opacity: 0,
}

const transitionStyles: any = {
  entering: { opacity: 1 },
  entered:  { opacity: 1 },
  exiting:  { opacity: 0 },
  exited:  { opacity: 0 },
};

function Fourth(props: any) {

  const [inProp, setInProp] = useState(false);

  useEffect(
    () => {
      const active = props.activeIndex;
      return () => {
        console.log(active)
        if(active === 4 || active === 2) {
          setInProp(true)
        } else {
          setInProp(false)
        }
      };
    },
    [props.activeIndex],
  );

  const nodeRef = useRef(null);
  return <div className='bg-black h-full text-white flex justify-center items-center relative md:pt-10'>
    <div className='relative z-10'>
      <Transition nodeRef={nodeRef} in={inProp} timeout={duration}>
        {state => (
          <div ref={nodeRef} style={{
            ...defaultStyle,
            ...transitionStyles[state]
          }}>
            <div className='bg-[url(@/assets/images/black_six.png)] w-[185px] h-[214px] text-white absolute -top-20 -right-60 flex justify-center text-center items-center text-2xl font-bold md:-right-24 md:bg-contain md:w-[92px] md:h-[107px] md:text-sm'>
              Research
            </div>
          </div>
        )}
        </Transition>
        <Transition nodeRef={nodeRef} in={inProp} timeout={duration}>
        {state => (
          <div ref={nodeRef} style={{
            ...defaultStyle,
            ...transitionStyles[state]
          }}>
            <div className='bg-[url(@/assets/images/black_six.png)] w-[185px] h-[214px] text-white absolute -bottom-60 left-16 flex justify-center text-center items-center text-2xl font-bold md:-bottom-32 md:left-8 md:bg-contain md:w-[92px] md:h-[107px] md:text-sm'>
              TECH
            </div>
          </div>
        )}
        </Transition>
        <Transition nodeRef={nodeRef} in={inProp} timeout={duration}>
        {state => (
          <div ref={nodeRef} style={{
            ...defaultStyle,
            ...transitionStyles[state]
          }}>
            <div className='bg-[url(@/assets/images/black_six.png)] w-[185px] h-[214px] text-white absolute -top-20 -left-60 flex justify-center text-center items-center text-2xl font-bold md:-left-24 md:bg-contain md:w-[92px] md:h-[107px] md:text-sm'>
              Marketing Operation
            </div>
          </div>
        )}
        </Transition>
      <div className='bg-[url(@/assets/images/white_six.png)] mb-[10px] bg-auto h-[370px] w-[320px] relative flex justify-center items-center text-black font-bold text-2xl md:w-[160px] md:h-[185px] md:bg-contain md:text-sm'>
        <div className='absolute'>Community</div>
      </div>
    </div>
    <img className='z-1 absolute -bottom-10 rotate-3 left-0' src={require('@/assets/images/line_left.png')} alt="" />
    <img className='z-1 absolute top-0 md:h-[600px]' src={require('@/assets/images/line_center.png')} alt="" />
    <img className='z-1 absolute -bottom-10 -rotate-3 right-0' src={require('@/assets/images/line_right.png')} alt="" />
  </div>;
}

export default Fourth;
