import './styles.less'
import React, { useState } from 'react' ;
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import First from './modules/First';
import Second from './modules/Second';
import Third from './modules/Third';
import Fourth from './modules/Fourth';
import Fifth from './modules/Fifth';
// import Sixth from './modules/Sixth'

function Index() {
  const [activeIndex, setActiveIndex] = useState(0)

  const slideChange = (v: any) =>  {
    console.log(v)
    setActiveIndex(v.activeIndex)
  }

  return <div className='min-h-[100vh]'>
      <Swiper
        className='w-full h-[100vh] overflow-auto'
        spaceBetween={0}
        slidesPerView={1}
        direction="vertical"
        onSlideChange={(v) => slideChange(v)}
        onSwiper={(swiper) => console.log(swiper)}
      >
        <SwiperSlide><First></First></SwiperSlide>
        <SwiperSlide><Second></Second></SwiperSlide>
        <SwiperSlide><Third></Third></SwiperSlide>
        <SwiperSlide><Fourth activeIndex={activeIndex}></Fourth></SwiperSlide>
        <SwiperSlide><Fifth></Fifth></SwiperSlide>
      </Swiper>
    {/* </div> */}
    
  </div>;
}

export default Index;
